body {
    line-height: 1.5;
    font-family: Arial, Helvetica, sans-serif;
}

#wrapper {
    position: relative;
    width: auto;
    min-height: 55vw;
    overflow: hidden;
}

.layer {
  position: absolute;
  width: 100vw;
  min-height: 55vw;
}

.layer h1 {
  font-size: 2em;
}

.layer .content-wrap {
  width: 100vw;
  min-height: 55vw;
}

.layer .content-body {
  position: absolute;
  top: 50%;
  /* text-align: center; */
  -webkit-transform: translate(30%, -50%);
          transform: translate(30%, -50%);
  color: #fff;
}

.layer video{
    width: 60%;
    -webkit-transform: skew(-30deg) translate(-50%, 0%);
            transform: skew(-30deg) translate(-50%, 0%);
}

.layer img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.top {
    background: #eee;
    z-index: -1;
}

.bottom {
    background: #222;
    z-index: -2;
}

.bottom .content-body {
  right: 15%;
}

.bottom h1 {
  color: #fdab00;
}


.skewed .top {
    -webkit-transform: skew(-30deg);
            transform: skew(-30deg);
    margin-left: -1000px;
    width: calc(50vw + 1000px);
}
    
.skewed .top .content-wrap {  
    -webkit-transform: skew(30deg);  
            transform: skew(30deg);
    margin-left: 1000px;
}

@media (max-width: 767px) {
  .skewed .top {
    margin-top: 30px;
}
  .layer h1 {
    margin-top: 80px;
    font-size: 100%;
  }
}
.shadow{
  box-shadow: 10px 15px 7px #C7C7C7;
}
.grow-shadow {
    box-shadow: 10px 15px 7px #C7C7C7;
  -webkit-transition: all .2s ease-in-out;
          transition: all .2s ease-in-out;
}

.grow-shadow:hover {
  -webkit-transform: scale(1.05);
      transform: scale(1.05);
  
}

.pchide{
  display: none;
}
@media (max-width:767px){
  .pchide{
    display: unset;
  }
  .sidenav {
    padding-top: 150px;
    height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  }
  .sidenav a {
    padding: 8px 8px 8px 32px;
  text-decoration: none;
  color: #818181;
  display: block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
    font-size: 18px;
  }
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 30;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }
  .mhide{
    display: none;
  }
}
section .container{
    padding: 50px;
}

section .section-title {
    text-align: center;
    margin-bottom: 50px;
    text-transform: uppercase;
}
#footer {
    background: rgb(41, 41, 41) !important;
}
#footer h5{
	padding-left: 10px;
    border-left: 3px solid #eeeeee;
    padding-bottom: 6px;
    margin-bottom: 20px;
    color:#ffffff;
}
#footer a {
    color: #ffffff;
    text-decoration: none !important;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}
#footer ul.social li{
	padding: 3px 0;
}
#footer ul.social li a i {
    margin-right: 5px;
	font-size:25px;
	-webkit-transition: .5s all ease;
	transition: .5s all ease;
}
#footer ul.social li:hover a i {
	font-size:30px;
	margin-top:-10px;
}
#footer ul.social li a,
#footer ul.quick-links li a{
	color:#ffffff;
}
#footer ul.social li a:hover{
	color:#eeeeee;
}
#footer ul.quick-links li{
	padding: 3px 0;
	-webkit-transition: .5s all ease;
	transition: .5s all ease;
}
#footer ul.quick-links li:hover{
	padding: 3px 0;
	margin-left:5px;
	font-weight:700;
}
#footer ul.quick-links li a i{
	margin-right: 5px;
}
#footer ul.quick-links li:hover a i {
    font-weight: 700;
}

@media (max-width:767px){
	#footer h5 {
    padding-left: 0;
    border-left: transparent;
    padding-bottom: 0px;
    margin-bottom: 10px;
}
}
